import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-ourbus-terms-condition',
  templateUrl: './ourbus-terms-condition.page.html',
  styleUrls: ['./ourbus-terms-condition.page.scss'],
})
export class OurbusTermsConditionPage implements OnInit {

  constructor(private navCtrl:NavController,private location: Location,
    private router: Router) { }

  ngOnInit() {
  }
  termsCondition(){
    this.navCtrl.navigateForward('term-conditions');
  }
  privacyPolicy(){
    this.navCtrl.navigateForward('privacy-policy');
  }
  handleBackButton() {
    // if (this.location.isCurrentPathEqualTo('/home')) {
    //   // Prevent navigating back to itself
    //   return;
    // }
    
    if (window.history.length > 1) {
      // If history is present, go back
      this.location.back();
    } else {
      // If no history, navigate to the home page
      this.router.navigate(['tabs/home']);
    }
  }
}
